<template>
	<div class="grid crud-demo">
		<div class="col-12">
			<div class="card">
				<Toast/>
				<Toolbar class="mb-4">
					<template v-slot:left>
						<div class="my-2">
							<Button label="New" icon="pi pi-plus" class="p-button-success mr-2" @click="openNew" />
							<Button label="Delete" icon="pi pi-trash" class="p-button-danger" @click="confirmDeleteSelected" :disabled="!selectedEntitys || !selectedEntitys.length" />
						</div>
					</template>

					<template v-slot:right>
						<FileUpload mode="basic" accept="image/*" :maxFileSize="1000000" label="Import" chooseLabel="Import" class="mr-2 inline-block" />
						<Button label="Export" icon="pi pi-upload" class="p-button-help" @click="exportCSV($event)"  />
					</template>
				</Toolbar>
				<DataTable ref="dt" :value="entitys" v-model:selection="selectedEntitys" dataKey="id" :paginator="true" :rows="10" :filters="filters"
							paginatorTemplate="FirstPageLink PrevPageLink PageLinks NextPageLink LastPageLink CurrentPageReport RowsPerPageDropdown" :rowsPerPageOptions="[5,10,25]"
							currentPageReportTemplate="Showing {first} to {last} of {totalRecords} entitys" responsiveLayout="scroll">
					<template #header>
						<div class="flex flex-column md:flex-row md:justify-content-between md:align-items-center">
							<h5 class="m-0">Administrar Mensajes</h5>
							<span class="block mt-2 md:mt-0 p-input-icon-left">
                                <i class="pi pi-search" />
                                <InputText v-model="filters['global']" placeholder="Buscar..." />
                            </span>
						</div>
					</template>
					<Column selectionMode="multiple" headerStyle="width: 3rem"></Column>
					<Column field="name" header="Mensaje" :sortable="true">
						<template #body="slotProps">
							<span class="p-column-title">Mensaje</span>
							{{slotProps.data.message}}
						</template>
					</Column>					
					<Column field="type" header="Whatsapp" :sortable="true">
						<template #body="slotProps">
							<span class="p-column-title">Whatsapp</span>
							{{slotProps.data.isWhatsapp}}
						</template>
					</Column>
					<Column field="address1" header="Correo" :sortable="true">
						<template #body="slotProps">
							<span class="p-column-title">Correo</span>
							{{slotProps.data.isEmail}}
						</template>
					</Column>				
					<Column field="address1" header="Mensajes Texto" :sortable="true">
						<template #body="slotProps">
							<span class="p-column-title">Mensajes Texto</span>
							{{slotProps.data.isSms}}
						</template>
					</Column>
					<Column field="address1" header="Activo" :sortable="true">
						<template #body="slotProps">
							<span class="p-column-title">Activo</span>
							{{slotProps.data.active}}
						</template>
					</Column>	
					<Column>
						<template #body="slotProps">
							<Button icon="pi pi-pencil" class="p-button-rounded p-button-success mr-2" @click="editEntity(slotProps.data)" />
							<Button icon="pi pi-trash" class="p-button-rounded p-button-warning" @click="confirmDeleteEntity(slotProps.data)" />
						</template>
					</Column>				
					
				</DataTable>
				
				<Dialog v-model:visible="entityDialog" :style="{width: '650px'}" header="InformaciÃ³n Mensajes" :modal="true" class="p-fluid">				
					<div class="formgrid grid">
						<div class="field col">
							<label for="name">Mensaje</label>
							<Textarea v-model="entity.message" :autoResize="true" rows="5" cols="30" />
						</div>						
					</div>
					<div class="field">
						<label for="state">Activar tipos de notificaciones</label>						
					</div>
					<div class="p-field-checkbox">
						<h5>Whatsapp</h5>						
						<InputSwitch v-model="entity.isWhatsapp" />
					</div>
					<div class="p-field-checkbox">
						<h5>Correo</h5>						
						<InputSwitch v-model="entity.isEmail" />						
					</div>
					<div class="p-field-checkbox">
						<h5>Mensajes de texto</h5>						
						<InputSwitch v-model="entity.isSms" />						
					</div>
					<div class="formgrid grid">
						<div class="field col">
							<label for="name">Frecuencia</label>
							<InputNumber v-model="entity.schedule" />
						</div>						
						<div class="field col">
							<h5>Activo</h5>														
							<InputSwitch v-model="entity.active" />
						</div>						
					</div>
					
					
					<template #footer>
						<Button label="Cancel" icon="pi pi-times" class="p-button-text" @click="hideDialog"/>
						<Button label="Save" icon="pi pi-check" class="p-button-text" @click="saveEntity" />
					</template>
				</Dialog>
				<Dialog v-model:visible="deleteEntityDialog" :style="{width: '450px'}" header="Confirm" :modal="true">
					<div class="flex align-items-center justify-content-center">
						<i class="pi pi-exclamation-triangle mr-3" style="font-size: 2rem" />
						<span v-if="entity">Desea eliminar el Mensaje <b>{{entity.name}}</b>?</span>
					</div>
					<template #footer>
						<Button label="No" icon="pi pi-times" class="p-button-text" @click="deleteEntityDialog = false"/>
						<Button label="Yes" icon="pi pi-check" class="p-button-text" @click="deleteEntity" />
					</template>
				</Dialog>

				<Dialog v-model:visible="deleteEntitysDialog" :style="{width: '450px'}" header="Confirm" :modal="true">
					<div class="flex align-items-center justify-content-center">
						<i class="pi pi-exclamation-triangle mr-3" style="font-size: 2rem" />
						<span v-if="entity">Desea eliminar los Mensajes?</span>
					</div>
					<template #footer>
						<Button label="No" icon="pi pi-times" class="p-button-text" @click="deleteEntitysDialog = false"/>
						<Button label="Yes" icon="pi pi-check" class="p-button-text" @click="deleteSelectedEntitys" />
					</template>
				</Dialog>	
			</div>
		</div>
	</div>

</template>

<script>
import MessageService from '../service/MessageService';

export default {
	data() {
		return {
			entitys: null,
			entityDialog: false,
			deleteEntityDialog: false,
			deleteEntitysDialog: false,
			entity: {},
			selectedEntitys: null,
			filters: {},
			submitted: false,
			
			permissions:null,
			picklistValue:null,
		}
	},
	messageService: null,
	created() {
		this.messageService = new MessageService();
	},
	mounted() { 
		this.loadEntitys();
	},
	methods: {		
		openNew() {
			this.entity = {};
			this.submitted = false;
			this.entityDialog = true;
		},
		hideDialog() {
			this.entityDialog = false;
			this.submitted = false;
		},
		saveEntity() {
			this.submitted = true;
			if (this.entity.message.trim()) {
				if (this.entity.id) {							
					this.messageService.updateMessage(this.entity).then(()=>{
					this.$toast.add({severity:'success', summary: 'Successful', detail: 'Message Actualizado', life: 3000});
					
					}).catch((data)=>{
						console.log(data.error);
						this.$toast.add({severity:'error', summary: 'Error Actualizando', detail: 'Error Message Actualizado', life: 3000});	
					});
					
				}else{		
					this.messageService.createdMessage(this.entity).then(()=>{
					this.$toast.add({severity:'success', summary: 'Successful', detail: 'Mensaje Creado con exito', life: 3000});
					}).catch((data)=>{
						console.log(data);
						this.$toast.add({severity:'error', summary: 'Error Creando ', detail: 'Error al creal el mensaje', life: 3000});	
					});
				this.loadEntitys();
				}	
			}
			this.entityDialog = false;
			this.entity = {};
							
		},
		editEntity(entity){	
			this.entity = {...this.deleteAttribNull(entity)};
			this.entityDialog = true;
		},
		confirmDeleteEntity(entity) {
			this.entity = entity;
			this.deleteEntityDialog = true;			
		},
		deleteEntity() {
			this.messageService.deleteMessage(this.entity).then(()=>{
					this.$toast.add({severity:'success', summary: 'Successful', detail: 'Message Eliminado', life: 3000});
						this.loadEntitys();
					}).catch(()=>{						
						this.$toast.add({severity:'error', summary: 'Error Eliminando ', detail: 'Error Message Actualizado', life: 3000});	
					})
			this.deleteEntityDialog = false;
			this.entity = {};
			
		},		
		exportCSV() {
			this.$refs.dt.exportCSV();
		},
		confirmDeleteSelected() {
			this.deleteEntitysDialog = true;
		},
		deleteSelectedEntity() {
			this.entitys = this.entitys.filter(val => !this.selectedEntitys.includes(val));
			this.deleteEntitysDialog = false;
			this.selectedentitys = null;
			this.$toast.add({severity:'success', summary: 'Successful', detail: 'Products Deleted', life: 3000});
		},
		loadEntitys(){
			this.messageService.getMessages().then(data => this.entitys = data);
		},
		deleteAttribNull(entity){
			Object.keys(entity).forEach(key => {
                    if (entity[key] === null) delete entity[key];
                 }); 
			return entity;	 
		}
	}
}
</script>

