import axios from 'axios'

export default class MessajeService {
    url = 'http://localhost:8080/messages/';
	createdMessage(message) {       
    return axios.post(this.url,message).then(res=>
        res.data        
      ); 
    }
    getMessages() {  
        return axios.get(this.url).then(res=>
          res.data        
        ); 
      }

    updateMessage(message) {
      return axios.patch(this.url+message.id,message).then(res=>
      res.data        
    ); 
  }
  getMessage(message) {
    return axios.get(this.url+message.id).then(res=>
    res.data        
    ); 
  }
  deleteMessage(message) {
    return axios.delete(this.url+message.id).then(res=>
    res.data        
  ); 
}
}
